.projects {
  padding: 4rem 2rem;
  color: var(--text);
  text-align: center;
}

.projects-tabs,
.projects-filters {
  display: flex;
  gap: 0.75rem;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 1.25rem;
}

.projects-filters {
  margin-top: 0.75rem;
}

.projects-tabs button,
.projects-filters button {
  border: 1px solid rgba(255, 255, 255, 0.12);
  background-color: var(--card);
  color: var(--text);
  padding: 0.55rem 1rem;
  border-radius: 999px;
  cursor: pointer;
  transition: background-color 0.2s ease, color 0.2s ease, border-color 0.2s ease, transform 0.15s ease;
}

.projects-tabs button:hover,
.projects-filters button:hover {
  border-color: rgba(255, 255, 255, 0.35);
}

.projects-tabs button.active,
.projects-filters button.active {
  background-color: var(--accent, #6c5ce7);
  border-color: var(--accent, #6c5ce7);
  color: #fff;
  transform: translateY(-1px);
}

.project-grid {
  display: grid;
  gap: 2rem;
  justify-content: center;
  grid-template-columns: repeat(auto-fit, minmax(280px, 320px));
  margin-top: 2rem;
  padding: 0 2rem;
  max-width: 1600px;
  margin-left: auto;
  margin-right: auto;
}

.projects-empty {
  grid-column: 1 / -1;
  color: var(--muted);
  margin-top: 1rem;
}

.project-card {
  background-color: var(--card);
  padding: 1.25rem;
  border-radius: 12px;
  text-align: left;
  transition: transform 0.2s ease, background-color 0.2s ease, box-shadow 0.2s ease;
  color: var(--text);
  text-decoration: none;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  overflow: hidden;
  width: 100%;
  max-width: 360px;
  box-sizing: border-box;
  border: 1px solid rgba(255, 255, 255, 0.05);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.25);
}

.project-badge {
  display: inline-block;
  margin: 0.2rem 0 0.6rem;
  padding: 0.3rem 0.65rem;
  border-radius: 999px;
  background: rgba(255, 255, 255, 0.08);
  color: var(--text);
  font-size: 0.8rem;
  border: 1px solid rgba(255, 255, 255, 0.12);
}

.project-card:hover {
  transform: translateY(-6px);
  background-color: var(--card-hover);
  box-shadow: 0 14px 36px rgba(0, 0, 0, 0.35);
}

.project-media-wrapper {
  position: relative;
  width: 100%;
  height: 220px;
  margin-bottom: 1rem;
  border-radius: 8px;
  overflow: hidden;
}

.project-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
  transition: transform 0.25s ease, opacity 0.3s ease;
}

.project-image.hidden {
  opacity: 0;
  pointer-events: none;
}

.project-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
  transition: opacity 0.3s ease;
}

.project-video.hidden {
  opacity: 0;
  pointer-events: none;
}

.project-video.visible {
  opacity: 1;
}

.project-link {
  color: inherit;
  text-decoration: none;
  display: block;
}

.project-card:hover .project-image {
  transform: scale(1.02);
}

.project-role {
  font-size: 0.9rem;
  color: var(--muted);
  margin-bottom: 0.5rem;
  line-height: 1.4;
}

.project-card p {
  font-size: 0.9rem;
  color: var(--muted);
  margin-bottom: 0.5rem;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  line-clamp: 3;
  -webkit-box-orient: vertical;
  line-height: 1.4;
}

@media (max-width: 768px) {
  .project-grid {
    grid-template-columns: 1fr;
  }
}
